body {
  color: black;
  font-family: Montserrat, sans-serif;
  font-size: 1rem;
}

.first-screen {
  max-width: 70vw;
  height: 100%;
  overflow: unset;
  background: #000000;
}

.services-block {
  width: 100%;
  height: auto;
  padding: 20px 0;
  background: rgba(203, 186, 210, 0.13);
}

.footer {
  width: 100%;
