.content {
  position: absolute;
  left: 0;
  width: 100%;
  text-align: center;
  padding: 20px 0;
  margin-top: 10%;
  margin-bottom: 40px; }

.title-block_item {
  color: #ffffff;
  padding: 15px 0;
  p {
    font-size: 35px;
    padding: 2px 0;
    line-height: 45px;
    text-align: center; } }

.logo-block_item {
  padding: 15px 0;
  width: 100%;
  svg {
    width: 311px;
    height: 110px; } }

.advertising_item {
  color: #ffffff;
  p {
    font-size: 35px;
    line-height: 26px;
    padding: 15px 0; } }

.call-to-action_item {
  padding: 48px 0;
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  a {
    text-decoration: none;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.04em;
    color: #fff;
    padding: 16px 70px;
    background: $buttonColorMain;
    border-radius: 5px;
    position: relative;
    z-index: 1;
    display: block;
    overflow: hidden;
    text-align: center;
    font-weight: 800;
    transition: all 0.2s linear;
    &--blue {}
    max-width: 400px;
    border: 2px solid $buttonColorMain;
    background: $buttonColorMain;
    &::before,
    &::after {
      position: absolute;
      top: 0;
      z-index: -1;
      display: block;
      width: 110%;
      height: 100%;
      content: "";
      transition: all 0.2s linear; }
    &::before {
      left: 0;
      background: $buttonColorMain; }
    &::after {
      left: -115%;
      background: #fff;
      transform: skewX(25deg); }
    &:hover {
      box-shadow: 0 0 10px 0 rgba($black, 0.3);
      color: $buttonColorMain;
      &::before,
      &::after {
        transform: translateX(100%) skewX(25deg); } }
    &:focus {
      outline: 0; } } }
