.about-info {
  padding: 71px 0; }

.about-photo_item {
  padding: 15px 0;
  &-person {
    padding: 5px 10px;
    width: 110px;
    height: 110px;
    overflow: hidden;
    transition: all 0.5s ease;
    img {
      max-width: 100px;
      height: auto; } } }
.line {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center; }
.about-info_title {
  padding: 25px 0;
  h2 {
    font-size: 110px;
    line-height: 49px;
    padding: 10px 25px; }
  p {
    font-size: 16px;
    line-height: 21px;
    text-align: left;
    max-width: 90%;
    padding: 10px 25px;
    margin-top: 55px; } }

.artur {
  transition: all 0.5s ease;
  background-size: cover;
  width: 180px;
  height: 180px;
  overflow: hidden;
  background-position: center;
  //background-position: 50% 50%
  background-image: url("./../img/Peoples/b&w/Artur-serious-1.jpg");
  &:hover {
    background-image: url("./../img/Peoples/Artur-smiled.jpeg");
    background-position: 50% 50%;
    background-size: cover;
    //max-width: 110px
    z-index: 9999; } }


