/*! .header */
.header {
  position: absolute;
  width: 100%;
  height: auto;
  background: transparent;
  .menu-block {
    padding: 20px;
    line-height: 29px;
    max-width: 100%;
    cursor: pointer;
    transition: all ease-in .5s;
    ul {
      display: flex;
      justify-content: flex-end;
      padding: 30px 0;
      li > a {
        font-size: 14px;
        padding: 0 30px;
        line-height: 18px;
        color: #f0f0f0;
        font-weight: 600;
        transition: all ease-in .5s;
        text-decoration: none;
        &:hover {
          color: #E51F1F; } } } } }



/*! .header end*/
