.services-block {
  &__title {
    width: 100%;
    height: auto;
    color: #000;
    h2 {
      font-size: 110px;
      line-height: 141px;
      padding: 25px 25px 0; } }
  &__about {
    padding: 20px;
    h3 {
      font-weight: 500;
      font-size: 40px;
      line-height: 100%;
      font-style: normal;
      padding: 20px 0; } }
  &__content {
    max-width: 428px;
    margin: 0 auto;
    height: auto;
    padding: 24px 0; }
  &__item-name {
    padding: 20px 0;
    margin-left: 19px;
    max-width: 1em; }
  &__item {
    display: flex;
    align-items: flex-end;
    width: 200px;
    height: 200px;
    p {
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 100%; } } }

.line-serv {
  margin: 20px 0; }
.content-part {
  p {
    padding: 17px 0; } }
.development-block, .smm, .influence, .context, .video, .marketing, .seo, .style, .audit {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  width: 143px;
  height: 170px;
  cursor: pointer;
  margin: 1px;
  transition: top 1s ease-out 0.5s; }

.line-serv {
  width: 100%;
  height: 4px;
  background: black; }

.development-block {
    background-image: url('./../img/-.png'); }

.development-content {
  position: relative;
  margin: 0 auto;
  width: 100%;
  height: 0;
  overflow: hidden;
  background: #fff;
  opacity: 0;
  transition: 3s;
  animation: move-up 1s;
  p {
    padding: 20px; }
  h4 {
    padding: 20px;
    background: #5119FF;
    color: #fff;
    font-size: 23px;
    line-height: 100%; } }

