/*! .footer */

.footer-content {
  width: 100%;
  height: auto;
  padding: 5px 0;
  display: flex;
  justify-content: space-between;
  flex-direction: row; }

.social-block {
  li {
    padding: 5px;
    font-size: 20px;
    a {
      text-decoration: none;
      color: #000;
      transition: linear .5s;
      &:hover {
        color: #E51F1F; } } } }

.contact-block, .social-block {
  padding: 10px 0;
  p {
    font-size: 20px;
    line-height: 23px;
    padding: 5px; } }

.footer-content-policy {
  border-top: 1px solid #000;
  p {
    font-size: 12px;
    line-height: 15px;
    padding: 25px 7px; } }


.social-block-icons {
  max-width: 213px;
  padding: 10px 0;
  span {
    font-weight: bold;
    font-size: 20px;
    line-height: 115%;
    color: #0F00B9; }
  ul {
    display: flex;
    li {
      padding: 10px 3px; } } }
.oun-width {
  width: 26px;
  height: auto;
  color: $buttonColorMain; }

.oun-width-youtube {
  width: 31px;
  height: auto;
  color: $buttonColorMain; }


/*! .footer end*/
