/*! Defaults */
body {
  color: $main-color;
  font-family: $default-font;
  @include font-size(16px); }

// sticky footer (always at the bottom of page)


.first-screen {
  max-width: 70vw;
  height: 100%;
  overflow: unset;
  background: #000000; }


.services-block {
  width: 100%;
  height: auto;
  padding: 20px 0;
  background: rgba(203, 186, 210, 0.13); }

.footer {
  width: 100%;
  height: auto;
  background: #E5E5E5;
  color: #000;
  text-align: left; }

.feedback {
  width: 100%;
  height: 100%;
  background: #E6DEFF; }


.loaded {
  #loader-wrapper {
    visibility: hidden;
    -webkit-transition: all .3s 1s ease-out;
    -o-transition: all .3s 1s ease-out;
    transition: all .3s 1s ease-out;
    -webkit-transform: translateY(-100%);
    -ms-transform: translateY(-100%);
    transform: translateY(-100%); } }

#loader-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0;
  background: #000; }


.loader {
  height: 100%;
  width: 100%; }

.loader .l_main {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 172px;
  height: 128px;
  margin: 0;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%); }

@media (max-width: 550px) {
  .loader {
    -webkit-transform: scale(0.75);
    transform: scale(0.75); } }

@media (max-width: 440px) {
  .loader {
    -webkit-transform: scale(0.5);
    transform: scale(0.5); } }
.l_square {
    position: relative; }
.l_square {
  &:nth-child(1) {
    margin-left: 0px; } }
.l_square {
  &:nth-child(2) {
    margin-left: 44px; } }
.l_square {
  &:nth-child(3) {
    margin-left: 88px; } }
.l_square {
  &:nth-child(4) {
    margin-left: 132px; } }
.l_square {
  span {
    position: absolute;
    top: 0px;
    left: 20px;
    height: 36px;
    width: 36px;
    border-radius: 2px;
    background-color: #FFFFFF; } }
.l_square {
  span:nth-child(1) {
    top: 0px; } }
.l_square {
  span:nth-child(2) {
    top: 44px; } }
.l_square {
  span:nth-child(3) {
    top: 88px; } }
.l_square:nth-child(1) {
  span {
    -webkit-animation: animsquare1 2s infinite ease-in;
    animation: animsquare1 2s infinite ease-in; } }
.l_square:nth-child(2) {
  span {
    -webkit-animation: animsquare2 2s infinite ease-in;
    animation: animsquare2 2s infinite ease-in; } }
.l_square:nth-child(3) {
  span {
    -webkit-animation: animsquare3 2s infinite ease-in;
    animation: animsquare3 2s infinite ease-in; } }
.l_square:nth-child(4) {
  span {
    -webkit-animation: animsquare4 2s infinite ease-in;
    animation: animsquare4 2s infinite ease-in; } }
.l_square {
  span:nth-child(1) {
    -webkit-animation-delay: 0.00s;
    animation-delay: 0.00s; } }
.l_square {
  span:nth-child(2) {
    -webkit-animation-delay: 0.15s;
    animation-delay: 0.15s; } }
.l_square {
  span:nth-child(3) {
    -webkit-animation-delay: 0.30s;
    animation-delay: 0.30s; } }
@-webkit-keyframes animsquare1 {
    0%,5%,95%,100% {
      -webkit-transform: translate(0px,0px) rotate(0deg);
      transform: translate(0px,0px) rotate(0deg); }
    30%,70% {
      -webkit-transform: translate(-40px,0px) rotate(-90deg);
      transform: translate(-40px,0px) rotate(-90deg); } }
@keyframes animsquare1 {
    0%,5%,95%,100% {
      -webkit-transform: translate(0px,0px) rotate(0deg);
      transform: translate(0px,0px) rotate(0deg); }
    30%,70% {
      -webkit-transform: translate(-40px,0px) rotate(-90deg);
      transform: translate(-40px,0px) rotate(-90deg); } }
@-webkit-keyframes animsquare2 {
    0%,10%,90%,100% {
      -webkit-transform: translate(0px,0px) rotate(0deg);
      transform: translate(0px,0px) rotate(0deg); }
    35%,65% {
      -webkit-transform: translate(-40px,0px) rotate(-90deg);
      transform: translate(-40px,0px) rotate(-90deg); } }
@keyframes animsquare2 {
    0%,10%,90%,100% {
      -webkit-transform: translate(0px,0px) rotate(0deg);
      transform: translate(0px,0px) rotate(0deg); }
    35%,65% {
      -webkit-transform: translate(-40px,0px) rotate(-90deg);
      transform: translate(-40px,0px) rotate(-90deg); } }
@-webkit-keyframes animsquare3 {
    0%,15%,85%,100% {
      -webkit-transform: translate(0px,0px) rotate(0deg);
      transform: translate(0px,0px) rotate(0deg); }
    40%,60% {
      -webkit-transform: translate(-40px,0px) rotate(-90deg);
      transform: translate(-40px,0px) rotate(-90deg); } }
@keyframes animsquare3 {
    0%,15%,85%,100% {
      -webkit-transform: translate(0px,0px) rotate(0deg);
      transform: translate(0px,0px) rotate(0deg); }
    40%,60% {
      -webkit-transform: translate(-40px,0px) rotate(-90deg);
      transform: translate(-40px,0px) rotate(-90deg); } }
@-webkit-keyframes animsquare4 {
    0%,20%,80%,100% {
      -webkit-transform: translate(0px,0px) rotate(0deg);
      transform: translate(0px,0px) rotate(0deg); }
    45%,55% {
      -webkit-transform: translate(-40px,0px) rotate(-90deg);
      transform: translate(-40px,0px) rotate(-90deg); } }
@keyframes animsquare4 {
    0%,20%,80%,100% {
      -webkit-transform: translate(0px,0px) rotate(0deg);
      transform: translate(0px,0px) rotate(0deg); }
    45%,55% {
      -webkit-transform: translate(-40px,0px) rotate(-90deg);
      transform: translate(-40px,0px) rotate(-90deg); } }

#top {
  position: fixed;
  right: 80px;
  bottom: 30px;
  cursor: pointer;

  //.arrow
  //  position: absolute
  //  bottom: 50%
  //  left: 50%
 }  //  margin-left: -20px

.arrow {
  &:before {
      content: '';
      position: absolute;
      top: 0;
      right: -24px;
      width: 30px;
      height: 7px;
      border-radius: 10px;
      display: block;
      background: #000;
      transform: rotate(45deg);
      -webkit-transform: rotate(45deg); } }
.arrow {
  &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -24px;
      width: 30px;
      height: 7px;
      border-radius: 10px;
      display: block;
      background: #000;
      transform: rotate(45deg);
      -webkit-transform: rotate(45deg); } }

.arrow {
  &:after {
    right: inherit;
    left: -24px;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg); } }


.animated {
  -webkit-animation: bounce 3s infinite;
  animation: bounce 3s both infinite; }



@-webkit-keyframes bounce {
  0% {
    -webkit-transform: translateY(0);
    opacity: 0; }
  60% {
    opacity: 1; }
  100% {
    -webkit-transform: translateY(-60px);
    opacity: 0; } }


@-moz-keyframes bounce {
    0% {
      -webkit-transform: translateY(0);
      opacity: 0; }
    60% {
      opacity: 1; }
    100% {
      -webkit-transform: translateY(-60px);
      opacity: 0; } }

@-o-keyframes bounce {
    0% {
      -webkit-transform: translateY(0);
      opacity: 0; }
    60% {
      opacity: 1; }
    100% {
      -webkit-transform: translateY(-60px);
      opacity: 0; } }

@keyframes bounce {
    0% {
      -webkit-transform: translateY(0);
      opacity: 0; }
    60% {
      opacity: 1; }
    100% {
      -webkit-transform: translateY(-60px);
      opacity: 0; } }





/*! Defaults End */

